import React, { useEffect } from "react";
import Router from "next/router";
import Countdown from "react-countdown";
import Confetti from "react-confetti";

const createRenderer = lang => {
  return ({ days, hours, minutes, seconds, completed }) => {
    if (completed) {
      // Render a completed state
      return <Confetti />;
    } else {
      // Render a countdown
      return (
        <span>
          {hours}:{minutes}:{seconds}
        </span>
      );
    }
  };
};

const Index = ({ language }) => {
  useEffect(() => {});

  //return <Countdown date={new Date(2020, 3, 20)} renderer={renderer} />;
  return (
    <Countdown date={Date.now() + 5000} renderer={createRenderer(language)} />
  );
};

Index.getInitialProps = async ({ res, req }) => {
  if (req && res) {
    console.log(req.headers.host);
    return {};
  } else {
    return {};
  }
};

export default Index;
